body {
  	margin: 0;
 	padding: 0;
	background-image:url(/demographic-app/static/media/background.203f08a1.png);
	background-repeat:no-repeat;
	background-size:cover;
	background-attachment: fixed;
  	font-family: 'roboto', sans-serif;
}


.App {
  text-align: center;
  display:flex-column;
  -webkit-justify-content: center;
          justify-content: center;
}

.imageForm{
	width:700px;
}

button{
	cursor:pointer;
}

.box{
	position:absolute;
	box-shadow:0 0 0 3px #149df2 inset;
	display:-webkit-flex;
	display:flex;
	-webkit-flex-wrap:wrap;
	        flex-wrap:wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor:pointer;
}

.center{
	display:-webkit-flex;
	display:flex;
	-webkit-justify-content: center;
	        justify-content: center;
}


input[type=file]::-webkit-file-upload-button {
 
  background: #bf8de8;
  color:white;
  padding:9px;
}

.submitBtn{
	background:#bf8de8;
}
